// Add here styles that are commonly used across multiple components. This file is imported in index.html file
// and the styles are applied globally.
@import "./vsan-utils.scss";
@import "./vsan-icons.scss";
@import "~@angular/cdk/_a11y.scss";

body {
   height: 100%;
   display: flex;
   flex-direction: column;
   // Fix trimming issues caused by zero right margin
   margin: 0 $vsan-outline-size;
}

vsan-root-component {
   router-outlet {
      display: none;
   }

   & > *:not(router-outlet) {
      display: flex;
      flex-direction: column;
      flex: 1 0 auto;
   }
}

// Set display properties for runtime generated components wrapped in ng-component
ng-component {
   display: flex;
   flex-direction: column;
   flex: 1 0 auto;
}

// Do not show the browser outline for programmatically focused elements
*[tabindex="-1"] {
   outline: none;
}

// Commonly used classes

.dialog-container {
   display: flex;
   flex-direction: column;
   flex: 1 0 0rem;
   @include child-bottom-spacing;
}

/*
  Horizontal & Vertical centering.
 */
.centered-content {
   display: flex;
   flex: 1 0 0rem;
   align-items: center;
   justify-content: center;
}

/*
  This style can be used, when the content must only be centered horizontally,
  while the above is useful for both horizontal & vertical alignment.
 */
.horizontally-centered-content {
   display: flex;
   flex: 0 0 auto;
   align-items: center;
   justify-content: center;
   @include sibling-right-spacing;
}

.bold-no-selection {
   font-weight: $vsan-font-weight-highlight;
   user-select: none;
}

.bold {
   font-weight: $vsan-font-weight-highlight;
}

.loading-wrapper {
   display: flex;
   flex-direction: column;
   flex: 1 0 auto;
   align-items: center;
   justify-content: center;
}

.central-spinner {
   position: absolute;
   left: 50%;
   top: 50%;
   margin-left: -$spinner-large-size/2;
   margin-top: -$spinner-large-size/2;

   &.spinner-md {
      margin-left: -$spinner-md-size/2;
      margin-top: -$spinner-md-size/2;
      // this styling is applied on .central-spinner-details if used with middle spinner to adjust its vertical position
      & + .central-spinner-details {
         // check central-spinner-details calculation
         margin-top: - $vsan-line-height-md/2 + $spinner-md-size/2 + $vsan-container-spacing;
      }
   }

   &.spinner-sm {
      margin-left: -$spinner-sm-size/2;
      margin-top: -$spinner-sm-size/2;
      // this styling is applied on .central-spinner-details if used with small spinner to adjust its vertical position
      & + .central-spinner-details {
         // check central-spinner-details calculation
         margin-top: - $vsan-line-height-md/2 + $spinner-sm-size/2 + $vsan-container-spacing;
      }
   }

   &.spinner-inline {
      margin-left: -$spinner-inline-size/2;
      margin-top: -$spinner-inline-size/2;
   }

   z-index: $vsan-z-index-layer-9;
}

// Used to display a message under the spinner. Use it with spinner
.central-spinner-details {
   position: absolute;
   // vertical alignment
   top: 50%;
   // because top: 50% the top corner of the message is vertically aligned.
   // $vsan-line-height-md is default text line height. - $vsan-line-height-md/2 aligns the message vertically (same as spinner)
   // + $spinner-large-size/2 moves the message at the bottom of the spinner
   // + $vsan-container-spacing adds default spacing between the spinner and message
   margin-top: - $vsan-line-height-md/2 + $spinner-large-size/2 + $vsan-container-spacing;
   // horizontal alignment
   left: 0;
   right: 0;
   text-align: center;
}

.disable-action {
   pointer-events: none;
   opacity: 0.4;
   * {
      pointer-events: none;
   }
}

.busy-backdrop {
   position: absolute;
   top: 0;
   bottom: 0;
   left: 0;
   right: 0;
   background-color: $background-color-backdrop;
   z-index: $vsan-z-index-layer-9;
}

.opaque-backdrop {
   background-color: $background-color-backdrop;
}

.right-float {
   float: right;
}

.text-nowrap {
   white-space: nowrap !important;
   overflow: hidden;
   text-overflow: ellipsis;
}

.icon-name-container {
   // Commonly used pattern in the UI is to have a container with icon + div or href tag with the name
   display: flex;
   flex: 0 0 auto;
   align-items: center;
   @include sibling-right-spacing;
}

.column-flex-container {
   display: flex !important;
   flex-direction: column !important;
   flex: 1 0 0rem !important;
}
/*
   Styles for clarity horizontal layout of controls that are not inside a form.
   E.g. clrFormLayout is not available.
   Use this to position a label and its associated control horizontally.
   Example usage:
      <clr-select-container class="horizontal-layout">
         <label>Example</label>
         <select clrSelect>
            <option....
            <option....
         </select>
      </clr-select-container>
*/
.horizontal-layout {
   display: flex;
   flex: 1 0 0rem;
   flex-direction: row !important;
   flex-wrap: wrap;
   @include sibling-right-spacing;

   .clr-form-control {
      flex-direction: row !important;
   }
}

/*
   Use this when you want label to get as much space as needed and what remaining to be for the clr control
   For example:
   <clr-input-container class="horizontal-layout label-auto-size">
      <label>Example</example>
      <input clrInput />
   </clr-input-container>
 */
.label-auto-size {
   .clr-control-container {
      display: flex;
      flex: 1 1 0rem;
      flex-direction: column;

      > * {
         display: flex;
         flex: 1 1 0rem;

         > *:not(.clr-validate-icon) {
            flex: 1 1 0rem;
         }
      }
   }
}

/*
   Use this when you want clr controls to be expanded on all available space of the parent
 */
.control-full-width {
   display: flex;
   flex: 1 1 0rem;

   .clr-control-container {
      display: flex;
      flex: 1 1 0rem;
      flex-direction: column;
      width: 100%;

      > * {
         display: flex;
         flex: 1 0 0rem;

         > *:not(.clr-validate-icon) {
            flex: 1 0 0rem;
         }
      }
   }
}

.hidden {
   display: none !important;
}

.indent {
   margin-left: $vsan-nested-indentation;
}

// Use when extra paddings/margins added by Clarity make the tag misplaced
.no-spacing {
   margin: 0 !important;
   padding: 0 !important;
}

// Use when extra margins added by Clarity are redundant,
// but the paddings should remain(e.g. there is a border around the element.
.no-margin {
   margin: 0 !important;
}

.small-btn {
   height: $vsan-line-height-md;
   line-height: $vsan-line-height-md;
   margin: 0 !important;
}

/*
   Add wide-item class on dropdowns in order to have longer menu items
 */
$max-menu-item-width: 60rem;
clr-dropdown.wide-item {
   clr-dropdown-menu {
      max-width: $max-menu-item-width;
   }
}

// keep highchart's tooltip always on top
.highcharts-tooltip-container {
   z-index: $vsan-z-index-layer-top !important;
}

.tooltip-action {
   padding: 0 !important;
   margin-top: 0 !important;
   margin-bottom: 0 !important;
}

.tooltip-padding-bottom {
   padding-bottom: $vsan-font-size-xxs;
}

.tooltip-value {
   padding-left: $vsan-xl;
   padding-right: $vsan-element-spacing/2;
   text-align: right;
}

$tooltip-colored-flag-width: 1.2rem;
$tooltip-colored-flag-height: 0.6rem;
$tooltip-colored-flag-padding-right: $vsan-xs;
.tooltip-color-flag {
   width: $tooltip-colored-flag-width;
   height: $tooltip-colored-flag-height;
   padding-right: $tooltip-colored-flag-padding-right;
}

.pointer {
   cursor: pointer;
}

.right-align-cell {
   text-align: right !important;
}

/*
   Remove clr-control-label when a grouping label is already rendered outside of the clr-control container
 */
.no-label-input-container {
   ::ng-deep .clr-control-label {
      display: none;
   }
}

// Hide LiveAnnouncer message from UI.
@include cdk-a11y();
