@import "vsan-utils.scss";

$datagrid-row-signpost-line-height: $vsan-xxl;
$signpost-height: 7.2rem;

// Fix alignment issue of signpost buttons
clr-signpost {
   display: inline-flex !important;
   height: $vsan-line-height-md;
   line-height: $vsan-line-height-md;

   .signpost-action {
      height: $vsan-line-height-md;
      line-height: $vsan-line-height-md;
      margin: 0;
   }

   // Make all signposts have max height and add scroller if that is exceeded
   .signpost-content .signpost-wrap .signpost-content-body {
      max-height: $signpost-height !important;
      overflow-y: auto;
      font-size: $vsan-font-size-sm;
      font-weight: $vsan-font-weight-normal;
      line-height: $vsan-line-height-md;
      white-space: normal;
      @include child-bottom-spacing;
   }
}

clr-datagrid {
   // By default set the Clarity datagrid to fill up the entire space.
   // If needed this can be overridden in specific views.
   flex: 1 0 0rem;
   // Remove extra space above all clarity datagrids
   padding-top: 0rem !important;

   // Fix truncation issue on active filters. There is a 14 px wrapper button around the 16px icon.
   .datagrid-filter-toggle {
      height: $vsan-icon-size-sm !important;
      width: $vsan-icon-size-sm !important;
   }

   &:not(.datagrid-compact) {
      .datagrid {
         .datagrid-column {
            // Fix for filter button misalignment. We need to increase the button height(0.7rem by default)
            // and reduce the vertical padding to retain the cell's height.
            padding-top: $vsan-sm;
            padding-bottom: $vsan-sm;

            .datagrid-filter-toggle {
               height: 1rem !important;
               margin-bottom: $vsan-outline-size-small;
            }
         }
      }
   }

   .datagrid {
      margin-top: 0rem;

      .datagrid-cell.datagrid-expandable-caret {
         // Keeps the caret top aligned on expand
         align-self: flex-start !important;

         .datagrid-expandable-caret-icon {
            margin-top: 0 !important;
         }
      }
   }

   // Add min-heigh, to make sure the datagrid isn't compressed to unusable state
   min-height: 180px;

   clr-dg-column {
      // This is needed for the show-title directive to work properly on datagrid columns.
      > .datagrid-column-flex {
         width: 100%;

         .datagrid-column-title {
            @include text-ellipsis;
         }
      }

      // Align headers content vertically
      &.datagrid-column .datagrid-column-flex {
         & > * :not(.datagrid-column-separator), // icons
         &::after, // sorting
         .datagrid-column-title { // title
            align-self: center;
         }
      }
   }

   // Vertical align datagrid's single-select radio
   clr-dg-row .datagrid-select .clr-checkbox-wrapper input[type=checkbox] + label,
   clr-dg-row .datagrid-select .clr-radio-wrapper input[type=radio] + label {
      &:before,
      &:after {
         top: 0px;
      }
   }

   .datagrid-row {
      // Align cells content vertically
      .datagrid-cell {
         align-self: center;
      }

      // Fixes misalignment content issue related to the default cell padding and signpost height
      &.signpost-row-height-fix .datagrid-cell {
         line-height: $datagrid-row-signpost-line-height;
         padding-top: 0;
         padding-bottom: 0;
      }
   }

   clr-dg-cell {
      // include spacing for icons to not stick to text
      clr-icon {
         margin-right: $vsan-icon-default-spacing;
      }

      // Remove top and bottom padding of datagrid cells with that class when the content inside (e.g., a clarity label
      // or chart) has a larger height that expands the row's height.
      &.datagrid-cell.no-vertical-padding {
         padding-top: 0;
         padding-bottom: 0;
      }

      // Apply any overflow rules coming from the show-title-directive to the nested elements
      & > * {
         text-overflow: inherit;
         overflow: inherit;
      }

      // Centers signpost of popup list when inside a datagrid cell
      $popup-list-height: 1.2rem;

      vsan-popup-list {
         height: $popup-list-height;
         line-height: $popup-list-height;
      }
   }

   // Compact datagrid
   &.datagrid-compact {
      .datagrid-header {
         // Fix issue with first datagrid row being pushed in the header and causing visual glitch on collapse/expand
         // of the columns due to the header's "sticky" position, but height 2px smaller than the content of the header.
         min-height: 1.4rem;
      }

      // fix for icons in details row which was partially hidden because negative margin top / bottom values
      clr-dg-row-detail, clr-dg-row .datagrid-cell clr-icon {
         margin-top: 0 !important;
         margin-bottom: 0 !important;
      }
   }

   // Fixes misalignment content issue related to the default cell padding and signpost height
   &.signpost-datagrid {
      .datagrid-cell {
         line-height: $vsan-xxl;
         padding: 0 $vsan-element-spacing;
      }
   }

   // fixes the thick bottom border of table header, if there is no content
   .datagrid-header + clr-dg-placeholder {
      border-top-width: 0rem !important;
   }

   // Centers empty datagrid placeholder
   .datagrid-table clr-dg-placeholder .datagrid-placeholder.datagrid-empty {
      justify-content: center;
   }
}

// Remove the extra padding-top from the header element inside clr-stack-view
clr-stack-view .stack-header .stack-title {
   padding-top: 0rem;
}

// Clarity cards have a huge margin-top, which isn't needed at all, since views make their own positioning.
.card {
   margin-top: 0rem;
}

// Clarity alerts have margin-top/bottom: .25rem, which makes them misaligned.
.alert, .card .alert {
   margin: 0;
}

h2, h3, h4, h5, h6 {
   margin-top: 0;
   margin-bottom: 0;
}

clr-button-group.btn-group.btn-link {
   margin-right: 0; // Override Clarity's .btn-group { margin-right: .5rem;} style
   @include sibling-right-spacing($vsan-button-spacing);
   height: $vsan-line-height-md;

   > .btn {
      min-width: auto;
      padding: 0 !important;
   }

   // Styles for ellipsis button which is used to display menu in button group.
   .btn-group-overflow {
      .dropdown-toggle {
         padding: 0;
         border: none;
         height: $vsan-line-height-md;
         line-height: $vsan-line-height-md;
      }
   }
}

.btn-icon {
   height: $vsan-icon-size-lg;
   line-height: $vsan-icon-size-lg;
   margin: 0 !important;
}

.user-tabbing {
   .btn-group .btn input[type="radio"]:focus {
      @include btn-outline-style($vsan-link-color);
   }

   button.btn:focus {
      @include btn-outline-style($vsan-link-color);
   }

   button.close:focus {
      @include btn-outline-style($vsan-link-color);

      // The X (close) icon is not visible because the fill color is dark in high contrast mode if the button is
      // focused. Need to overwrite the color so the x icon is visible.
      clr-icon {
         fill: $vsan-gray !important;
      }
   }

   .clr-toggle-wrapper input[type=checkbox]:focus + label::before,
   .clr-radio-wrapper input[type=radio]:focus + label::before,
   .clr-checkbox-wrapper input[type=checkbox]:focus + label::before {
      @include btn-outline-style($vsan-link-color);
   }

   // The cdk-high-contrast-active is added to body by @angular/cdk when OS has high contrast turned on
   // Note: @angular/cdk CAN NOT detect high contrast mode enabled by the chrome highcontrast extension.
   &.cdk-high-contrast-active {
      .datagrid-filter .clr-input:focus {
         @include btn-outline-style($vsan-link-color);
      }
   }
}

// The clarity radio is not visible in high contrast mode if it's checked because high contrast mode doesn't support any
// background color or shadow, but clarity uses border shadow to render the checked radio button.
// This style is a workaround, by setting the border of the radio button user is able to see the checked radio
.cdk-high-contrast-active {
   .clr-radio-wrapper input[type=radio]:checked + label::before {
      border: $high-contrast-radio-border solid $vsan-border-default-color;
   }
}

// Clarity forms fixes
form {
   @include child-bottom-spacing;
}

.clr-form, .clr-form-group {
   padding: 0;
}

.clr-control-label {
   // Align the form label to the form control elements in horizontal forms
   line-height: $vsan-line-height-md !important;
}

.clr-form-control {
   margin-top: 0; // By default the select box has 1rem margin
}

// Fix issue with horizontal scroller appearing in horizontal forms and grids
.clr-row {
   margin-right: 0;

   .clr-control-container {
      padding-right: 0;
   }
}

.table {
   margin-top: 0;
}

// Make sure Clarity tabs content is not too close to the tab links
.tab-content {
   display: flex !important;
   flex-direction: column;
   flex: 1 0 auto;
   padding-top: $vsan-element-spacing;
   padding-bottom: $vsan-outline-size;
   // Add min height to avoid content being squished.
   min-height: 200px;
}

// There is a gap between the top of the tabs and the text inside them.
// Because of it when we add the space between the above elements and the tabs
// it's looking like there is huge empty space between the elements.
// To fix that without affecting the original tabs size we use negative top margin.
// This only works when the tabs are not the first child element in the component.
clr-tabs:not(:first-child) {
   margin-top: -$vsan-element-spacing;
}

.p1, .p2, .p3, .p4, .p5, .p6, .p7 {
   margin-top: 0 !important;
}

// Clarity dropdow-item elements using css cannot be properly disabled
.dropdown-item[disabled] {
   cursor: not-allowed;
   opacity: $vsan-disabled-element-opacity;
   user-select: none;

   label {
      cursor: not-allowed;
   }
}

// Fix labels and badges accessibility issues - replace colors with Clarity v.4
.label.label-info,
a.label.label-info {
   background: $label-info-background-color !important;
   color: $label-info-text-color !important;
   border-color: $label-info-border-color !important;
}

.label.label-success,
a.label.label-success {
   background: $label-success-background-color !important;
   color: $label-success-text-color !important;
   border-color: $label-success-border-color !important;
}

.label.label-warning,
a.label.label-warning {
   background: $label-warning-background-color !important;
   color: $label-warning-text-color !important;
   border-color: $label-warning-border-color !important;
}

.label.label-danger,
a.label.label-danger {
   background: $label-danger-background-color !important;
   color: $label-danger-text-color !important;
   border-color: $label-danger-border-color !important;
}

.badge.badge-info {
   background: $badge-info-background-color !important;
   color: $badge-info-text-color !important;
}

.badge.badge-success {
   background: $badge-success-background-color !important;
   color: $badge-success-text-color !important;
}

.badge.badge-warning {
   background: $badge-warning-background-color !important;
   color: $badge-warning-text-color !important;
}

.badge.badge-danger {
   background: $badge-danger-background-color !important;
   color: $badge-danger-text-color !important;
}