// These correspond to Clarity's clr-col sizes
$breakpoints-shrink: (
      'sm': (max-width: 576px),
      'md': (max-width: 768px),
      'lg': (max-width: 992px),
      'xl': (max-width: 1200px),
      'sm-v': (max-height: 767px)
) !default;

$breakpoints-expand: (
      'sm': (min-width: 576px),
      'md': (min-width: 768px),
      'lg': (min-width: 992px),
      'xl': (min-width: 1200px),
      'sm-v': (min-height: 767px)
) !default;

@mixin respond-to-shrink($breakpoint) {
   @if map-has-key($breakpoints-shrink, $breakpoint) {
      @media #{inspect(map-get($breakpoints-shrink, $breakpoint))} {
         @content;
      }
   } @else {
      @warn "Unfortunately, no value could be retrieved from `#{$breakpoint}`. "
        + "Available breakpoints are: #{map-keys($breakpoints-shrink)}.";
   }
}

@mixin respond-to-expand($breakpoint) {
   @if map-has-key($breakpoints-expand, $breakpoint) {
      @media #{inspect(map-get($breakpoints-expand, $breakpoint))} {
         @content;
      }
   } @else {
      @warn "Unfortunately, no value could be retrieved from `#{$breakpoint}`. "
        + "Available breakpoints are: #{map-keys($breakpoints-expand)}.";
   }
}
