@import "../../../../css/vsan-defaults";

$vsan-show-title-padding: $vsan-element-spacing/2;
$vsan-show-title-height: $vsan-line-height-md;
$vsan-show-title-width: 1.5rem;

[vsan-show-title-multi-line] {
   & > vsan-clipboard-icon {
      background-color: $background-color;
      height: $vsan-show-title-height;
   }
   .custom-ellipsis {
      display: inline-block;
      // The parent is the relative element. The relative position is set/removed in the directive itself.
      position: absolute;
      height: $vsan-show-title-height;
      width: $vsan-show-title-width;
      text-align: center;
      // Aligns ellipsis to the bottom
      padding-top: $vsan-show-title-padding;
      // Unifies left and right ellipsis spacing when copyToClipboard icon is visible.
      padding-right: $vsan-show-title-padding;
      background-color: $background-color;
   }
}
