@import "./vsan-colors.scss";

/* Defaults */

// ---------------------------------------------
// Default font-size from Clarity UI v.3.0.0
// ---------------------------------------------
//       html {
//          font-size: 20px;
//       }
// ---------------------------------------------

// Spacings
$vsan-xxl: 1.8rem !default;   // 36px
$vsan-xl: 1.2rem !default;    // 24px
$vsan-lg: 0.9rem !default;    // 18px
$vsan-md: 0.6rem !default;    // 12px
$vsan-sm: 0.45rem !default;   // 9px
$vsan-xs: 0.3rem !default;    // 6px
$vsan-xxs: 0.15rem !default;  // 3px
$vsan-xxxs: 0.05rem !default; // 1px
$vsan-0: 0rem !default;

$vsan-element-spacing: $vsan-md;
$vsan-container-spacing: $vsan-element-spacing*2;
$vsan-button-spacing: $vsan-element-spacing*2;
$vsan-button-group-bottom-spacing: $vsan-element-spacing/2;
// For nesting elements within a view
$vsan-nested-indentation: $vsan-xl;
// The dropdown items already have 1.2rem padding, so to have nested items we need 1.8rem indentation
$vsan-dropdown-nested-indentation: $vsan-xxl;
// Use this outline size in dialogs/modals/pages, where we have a component like checkbox, that has a
// background color, which otherwise gets truncated.
$vsan-outline-size: $vsan-xxs;
$vsan-outline-size-small: $vsan-xxxs;

// Icons
$vsan-icon-size-xs: 0.7rem !default;   //14px
$vsan-icon-size-sm: 0.8rem !default;   //16px
$vsan-icon-size-md: 1rem !default;     //20px
$vsan-icon-size-lg: 1.2rem !default;   //24px
$vsan-icon-size: $vsan-icon-size-sm !default;     //16px
$vsan-icon-default-spacing: $vsan-xs;      // The space between icon and related text, etc.

// Borders
$vsan-border-position-all: all !default;
$vsan-border-default-size: $vsan-xxxs !default;
$vsan-border-default-pattern: solid !default;
$vsan-border-default-color: var(--vsan-border-color) !default;
$vsan-border: $vsan-border-default-size  $vsan-border-default-pattern $vsan-border-default-color;
$border-highlight-color: var(--vsan-link-color);
// Border Radius
$vsan-border-radius-default-size: $vsan-xxs;
$vsan-border-radius-medium-size: $vsan-sm;
$vsan-border-radius-small-size: $vsan-xxxs;

// Background & colors
$background-color-main: var(--vsan-main-background-color);
$background-color: var(--vsan-background-color);
$background-color-selected: var(--vsan-background-color-selected);
$background-color-hover: var(--vsan-background-color-hover);
$background-color-button-hover: var(--vsan-background-color-button-hover);
$background-color-backdrop: var(--vsan-busy-backdrop-background-color);
$disabled-color: $vsan-light-midtone-gray;
$vsan-link-color: var(--vsan-link-color);
$vsan-font-color-emphasize: var(--vsan-font-color-emphasize);
$vsan-hover-link-color: var(--vsan-link-color-hover);
$vsan-table-row-border-color: var(--vsan-table-row-border-color);

// Clarity v4 colors in order to resolve some accessibility issues
$label-info-text-color: var(--clarity-v4-label-info-text-color);
$label-info-background-color: var(--clarity-v4-label-info-background-color);
$label-info-border-color: var(--clarity-v4-label-info-border-color);
$label-success-text-color: var(--clarity-v4-label-success-text-color);
$label-success-background-color: var(--clarity-v4-label-success-background-color);
$label-success-border-color: var(--clarity-v4-label-success-border-color);
$label-warning-text-color: var(--clarity-v4-label-warning-text-color);
$label-warning-background-color: var(--clarity-v4-label-warning-background-color);
$label-warning-border-color: var(--clarity-v4-label-warning-border-color);
$label-danger-text-color: var(--clarity-v4-label-danger-text-color);
$label-danger-background-color: var(--clarity-v4-label-danger-background-color);
$label-danger-border-color: var(--clarity-v4-label-danger-border-color);
$vsan-focus-outline-color: var(--vsan-focus-outline-color);
$vsan-focus-outline-selected-row-color: var(--vsan-focus-outline-selected-row-color);

$badge-info-text-color: var(--clarity-v4-badge-info-text-color);
$badge-info-background-color: var(--clarity-v4-badge-info-background-color);
$badge-success-text-color: var(--clarity-v4-badge-success-text-color);
$badge-success-background-color: var(--clarity-v4-badge-success-background-color);
$badge-warning-text-color: var(--clarity-v4-badge-warning-text-color);
$badge-warning-background-color: var(--clarity-v4-badge-warning-background-color);
$badge-danger-text-color: var(--clarity-v4-badge-danger-text-color);
$badge-danger-background-color: var(--clarity-v4-badge-danger-background-color);

// status colors
$status-success-background-color: var(--vsan-status-success-bg-color);
$status-success-details-color: var(--vsan-status-success-details-color);
$status-success-border-color: var(--vsan-status-success-border-color);
$status-info-background-color: var(--vsan-status-info-bg-color);
$status-info-details-color: var(--vsan-status-info-details-color);
$status-info-border-color: var(--vsan-status-info-border-color);
$status-info-inner-color: var(--vsan-status-info-inner-color);
$status-warning-background-color: var(--vsan-status-warning-bg-color);
$status-warning-details-color: var(--vsan-status-warning-details-color);
$status-warning-border-color: var(--vsan-status-warning-border-color);
$status-error-background-color: var(--vsan-status-error-bg-color);
$status-error-details-color: var(--vsan-status-error-details-color);
$status-error-border-color: var(--vsan-status-error-border-color);
$status-unknown-background-color: var(--vsan-status-unknown-bg-color);
$status-unknown-background-secondary-color: var(--vsan-status-unknown-bg-secondary-color);
$status-unknown-details-color: var(--vsan-status-unknown-details-color);
$status-unknown-border-color: var(--vsan-status-unknown-border-color);
$status-details-color: var(--vsan-status-details-color);

// Icon colors
$icon-fill-color: var(--icon-fill-color);

// Font
$vsan-font-size-xxs: 0.5rem !default;  // 10px
$vsan-font-size-xs: 0.55rem !default;  // 11px
$vsan-font-size-sm: 0.65rem !default;  // 13px
$vsan-font-size-md: 0.7rem !default;   // 14px
$vsan-font-size-lg: 0.9rem !default;   // 18px
$vsan-font-size-xl: 1.2rem !default;   // 24px
$vsan-font-default-color: var(--vsan-color);
$vsan-line-height-md: $vsan-xl;
$vsan-line-height-sm: 0.8rem;   //16px
$vsan-relative-line-height-xs: 1em;
$vsan-relative-line-height-sm: 1.1em;
$vsan-relative-line-height-md: 1.3em;
$vsan-relative-line-height-xl: 1.5em;
$vsan-relative-line-height-xxl: 2em;
$vsan-font-weight-strong: 600;
$vsan-font-weight-highlight: 500;
$vsan-font-weight-normal: 400;

// Z-indexes
$vsan-z-index-layer-1: 100;
$vsan-z-index-layer-2: 200;
$vsan-z-index-layer-3: 300;
$vsan-z-index-layer-4: 400;
$vsan-z-index-layer-5: 500;
$vsan-z-index-layer-6: 600;
$vsan-z-index-layer-7: 700;
$vsan-z-index-layer-8: 800;
$vsan-z-index-layer-9: 900;
// Used to keep the element always on the top layer. Do not create constant with bigger value
$vsan-z-index-layer-top: 1000;

// Opacity
$vsan-disabled-element-opacity: 0.54;

// Spinners - the size is taken from Clarity's documentation v.2. They will change in v.3
$spinner-sm-size: 0.9rem;
$spinner-inline-size: 0.9rem;
$spinner-md-size: 1.8rem;
$spinner-large-size: 3.6rem;

// Cards layout defaults
$vsan-card-width: 24rem;
$vsan-card-max-width: 36rem;

// Checked radio button border width in high contrast mode
$high-contrast-radio-border: $vsan-border-default-size * 5; // high contrast mode border side 0.25rem
