@import "./vsan-defaults.scss";

@mixin add-border-radius ($radius-top-left: $vsan-border-radius-default-size,
                          $radius-top-right: $vsan-border-radius-default-size,
                          $radius-bottom-right: $vsan-border-radius-default-size,
                          $radius-bottom-left: $vsan-border-radius-default-size) {
   border-radius: $radius-top-left $radius-top-right $radius-bottom-right $radius-bottom-left;
}

@mixin text-ellipsis {
   overflow: hidden;
   text-overflow: ellipsis;
   white-space: nowrap;
}

// Add button focus status indicator.
@mixin button-focus-state($color: $vsan-link-color) {
   border: $vsan-border-default-size $vsan-border-default-pattern $color !important;
   box-shadow: 0 0 $vsan-outline-size $color;
}

/*
   Add button focus indicator with outline.
   In high contrast mode, the border is not visible.
   We should use an outline to show focused elements in that case.
*/
@mixin btn-outline-style($color: $vsan-link-color) {
   outline-offset: $vsan-outline-size-small !important;
   outline: $vsan-outline-size-small * 2 solid $color !important;
}

// Add card drag state indicator.
@mixin card-move-state($color: $vsan-link-color) {
   border: $vsan-border-default-size $vsan-border-default-pattern $color !important;
   box-shadow: 0 $vsan-outline-size 0 0 $color;
}

@mixin draggable-card {
   display: flex;
   flex-direction: column;
   flex: 0 0 auto;
   min-width: $vsan-card-width;
}

/**
   Include this mixin at :host level to make every top level component in the view
   have a bottom margin, besides the last one.
 */
@mixin child-bottom-spacing($element-spacing: $vsan-element-spacing) {
   > * {
      margin-bottom: $element-spacing !important;
   }
   > clr-button-group,
   > .vsan-actions {
      // Special handling of clr-button-groups
      margin-bottom: $vsan-button-group-bottom-spacing !important;
   }
   > *:last-child {
      margin-bottom: 0rem !important;
   }
}

@mixin sibling-right-spacing($element-spacing: $vsan-element-spacing) {
   & > * {
        margin-right: $element-spacing !important;
   }
   & > clr-signpost {
      // When the element is a signpost reduce the spacing prior/after it.
      // Unfortunately there is no "previous sibling" selector
      // so the only way to fix the previous element's spacing is to add negative margin-left
      margin-right: $vsan-icon-default-spacing !important;
      &:not(:first-child) {
         margin-left: -$element-spacing + $vsan-icon-default-spacing !important;;
      }
   }
   & > clr-icon {
      // Special handling of clr-icons
      margin-right: $vsan-icon-default-spacing !important;
      // if there is an element before the icon, keep it closer to it. Same as rule above.
      &:not(:first-child) {
         margin-left: -$element-spacing + $vsan-icon-default-spacing !important;;
      }
   }
   > *:last-child {
      margin-right: 0rem !important;
   }
}

// Background style with linear gradient to imitate stripes
@mixin no-capacity-background($size: 5px, $color: var(--vsan-color)) {
   background-image: linear-gradient(45deg, transparent 34%, $color 34%, $color 51%, transparent 51%, transparent 84%, $color 84%,  $color 100%);
   background-size: $size $size;
}

@mixin selected-entity-font-style() {
   font-family: 'Metropolis';
   font-weight: $vsan-font-weight-strong;
   font-style: italic;
}

// Creates a circle
@mixin circle($size: 0.6rem, $background: $background-color-main, $border: $vsan-border) {
   background: $background;
   border: $border;
   width: $size;
   height: $size;
   border-radius: 50%;
   display: flex;
   align-items: center;
   justify-content: center;
}
