@import "./vsan-defaults.scss";
@mixin vsan-icon($image_path) {
  background: url($image_path);
  background-size: 100%;
}

.no-icon {
  padding-left: $vsan-icon-size;
}

.silent-check {
  @include vsan-icon("../../images/svg/silent-check.svg");
}

.skipped-check {
   @include vsan-icon("../../images/svg/skipped-check.svg");
}

.spinner.spinner-icon {
   height: $vsan-icon-size;
   width: $vsan-icon-size;
   min-height: $vsan-icon-size;
   min-width: $vsan-icon-size;
}

// removes the reserved space for the icon. If used with shape Icon.EMPTY_ICON it will looks like there is no rendered
// dom element.
.no-icon {
   height: 0px;
   width: 0px;
   min-height: 0px;
   min-width: 0px;
   display: none;
}

.is-disabled {
   fill: $disabled-color;
}